// @ts-ignore
import {JqlApi} from "./mdk/jql-api.js";
// @ts-ignore
import {JqlSchema, TextColumn, NumberColumn, DateColumn, Dropdown} from "./mdk/jql-schema.js";
// @ts-ignore
import {http} from "@/mdk/auth-api";
// import confirmDatePlugin from "flatpickr/dist/plugins/confirmDate/confirmDate";

const required = (v: string | null, entity: any) => {
    return (v != null && v != '') ? null : " "
}

const haveRoles = (v: string | string[], entity: any) => {
    console.log(v, entity)
    return v.includes(',') ? null : ' (,)  .'
}

const onlyNum = (v: string, entity: any) => {
    return (!isNaN(parseFloat(v))) ? null : '  .'
}

// Example
const containerSchema = new JqlSchema([
    new TextColumn('id', ' ID'),
    new TextColumn('name', ' '),
    new TextColumn('type', ' '),
    new TextColumn('version', ''),
    new TextColumn('resister', ''),
    new TextColumn('created', ''),
    new TextColumn('status', ''),
    new TextColumn('description', ''),
    new NumberColumn('downloadCnt', '  '),
    new TextColumn('lastDownload', ''),
    new TextColumn('link', ' Link'),
]);

const fileSchema = new JqlSchema([
    new NumberColumn('fid', ' ID'),
    new TextColumn('name', ' '),
    // new TextColumn('type', ''),
    // new NumberColumn('version', ''),
    new TextColumn('createdby', ''),
    new TextColumn('created', ''),
    // new TextColumn('status', ''),
    new TextColumn('description', ''),
    new DateColumn('lastDownloaded', ''),
    new TextColumn('link', ' Link'),
    new TextColumn('file', '')
]);

const robotSchema = new JqlSchema([
    new TextColumn('rid', ' ID'),
    new TextColumn('name', ' '),
    new Dropdown('type', ' ', {COBOT: 'COBOT', ARM_TOW: 'ARM_TOW',PALLETIZER: 'PALLETIZER', AMR_LIFT: 'AMR_LIFT'}),
    new TextColumn('location', ' '),
    new TextColumn('createdby', ''),
    new TextColumn('created', ''),
])

